@import 'ScssHelpers/helpers-all.scss';

.event_type {
	text-align: center;
}

.event_type__wrap {
	position: relative;
}

.event_type__title {
	font-weight: 700;
	font-size: 1.6rem;
	line-height: 262%;
	letter-spacing: 0.2em;
	text-transform: uppercase;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4.2rem;
		}

		@include bp($point_3) {
			margin-bottom: 2.2rem;
		}
	}
}

.event_type__list {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 9rem;
		}

		@include bp($point_3) {
			margin-bottom: 4rem;
		}
	}
}

.event_type__item {
	&:not(:last-child) {
		@include bp($point_3) {
			margin-bottom: 2rem;
		}
	}
}

.event_type__link {
	position: relative;
	z-index: 2;
	display: flex;
	align-items: center;
	flex-direction: column;

	line-height: 120%;
	font-family: $font_2;
	color: $black_2;
	text-shadow: 1px 1px 5px $white_2;

	cursor: pointer;

	&::after {
		content: attr(data-text);
		height: 0;
		visibility: hidden;
		overflow: hidden;
		user-select: none;
		pointer-events: none;
		font-weight: 900;

		@media speech {
			display: none;
		}
	}

	&:focus,
	&:hover {
		text-decoration: none;
	}

	@media (any-hover: hover) {
		&:hover {
			font-weight: 900;
			letter-spacing: 0.01em;
		}
	}

	@include bp($point_2, min) {
		font-weight: 500;
		font-size: 8.2rem;
	}

	@include bp($point_3) {
		font-weight: 700;
		font-size: 3.2rem;
	}
}

.event_type__item_img_top {
	position: absolute;
	top: 0;

	&:not(.event_type__item--img_mod &) {
		left: 0;
	}

	.event_type__item--img_mod & {
		right: 0;
	}
}

.event_type__item_img_bottom {
	bottom: 0;
	right: 0;
	// z-index: 3;

	&:not(.event_type__item--img_mod &) {
		right: 0;
	}

	.event_type__item--img_mod & {
		left: 0;
	}
}

.event_type__item_img_top,
.event_type__item_img_bottom {
	position: absolute;
	width: auto;
	height: auto;

	max-width: 52rem;
	max-height: 60rem;

	border-radius: $gl_radius;

	opacity: 0;
	pointer-events: none;

	transition: opacity 0.3s ease;

	.event_type__item:hover &,
	.event_type__item:focus & {
		opacity: 1;
	}

	@include bp($point_1 - 1) {
		display: none;
	}
}
